<template>
    <div class="container">
        <v-expansion-panel>
            <v-expansion-panel-content>
                <div slot="header">User List</div>
                <v-tabs
                        v-model="active"
                        color="cyan"
                        dark
                        slider-color="yellow"
                >
                    <v-tab
                            v-for="(user,i) in users"
                            :key="i"
                            ripple
                    >
                        {{ user }}
                    </v-tab>
                    <v-tab-item
                            v-for="n in 3"
                            :key="n"
                    >
                        <v-card flat>
                            <v-card-text>{{ text }}</v-card-text>
                        </v-card>
                    </v-tab-item>
                </v-tabs>
            </v-expansion-panel-content>
            <v-expansion-panel-content>
                <div slot="header">Top 5 Forms</div>
                <v-card>
                    <v-card-text>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim
                        veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.
                    </v-card-text>
                </v-card>
            </v-expansion-panel-content>
        </v-expansion-panel>
    </div>
</template>

<script>
    import axios from 'axios'

    export default {
        name: "Home",
        data() {
            return {
                active: null,
                users: [],
                forms: null,
                text: 'test'
            }
        }, methods: {
            next() {
                const active = parseInt(this.active)
                this.active = (active < 2 ? active + 1 : 0).toString()
            }
        },
        created() {
            axios.get('/GetUsers')
                .then(data => {
                    this.users = data.data;
                    console.log(data.data)
                }).catch(error => {
                console.log(error);
            });
            // axios.get('/GetTopForms')
            //     .then(data => {
            //         this.users = data.data;
            //     }).catch(error => {
            //     console.log(error);
            // })
        }
    }
</script>

